import { Component, OnInit } from '@angular/core';
import {FetchfaqService} from './fetchfaq.service';
@Component({
  selector: 'app-faq',
  templateUrl: './faq.component.html',
  styleUrls: ['./faq.component.css'],
  providers:[FetchfaqService]
})
export class FaqComponent implements OnInit {
faqDetails;

  constructor(private faqdetails:FetchfaqService) { }

  ngOnInit() {
    localStorage.setItem("state","FAQs");
    this.faqdetails.fetch_faq_details().subscribe(response=>{
      this.faqDetails=response;

    });
  }

}
