import { Component, OnInit } from '@angular/core';
import { FormGroup,FormControl,FormBuilder,Validators,FormArray } from '@angular/forms';
import { GetdataService } from './getdata.service';
import {Router} from "@angular/router";
import {FetchbookingsService} from '../../booking-details/my-bookings/fetchbookings.service';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers:[GetdataService,FetchbookingsService]
})
export class LoginComponent implements OnInit {
user_details:FormGroup;
invalid:boolean=false;
bookingCount;

// ******A dummy object that will be passed with get request as parameters.***********

credentials={
    "Email_Id": "ragnar@lothbrok.com",
    "Password": "alpha"
};

// ******injecting service for fetching the data for users after successful login**********

  constructor(private form_builder:FormBuilder,
  private getdata:GetdataService,  private route:Router,private count_bookings: FetchbookingsService) { }

  ngOnInit() {

// ******** count number of bookings of the logged in user past as well as upcomings*********


    this.count_bookings.fetch_booking_details().subscribe(results=>{
    this. bookingCount=results[0].past.length + results[0].upcoming.length;


    });

    this.user_details=this.form_builder.group(
      {
// ******* Validations for email and password***********

        Email:['',[Validators.required,Validators.pattern('^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$')]],
        Password:['',[Validators.required,Validators.minLength(6),Validators.maxLength(12)]]



});
}

// ************  Function to check whether logged in user is there in our database or not*******

matchdetails(e)
{
  this.credentials.Email_Id=this.user_details.get('Email').value;
  this.credentials.Password=this.user_details.get('Password').value;
  this.getdata.checkuser(this.credentials).subscribe(result=>{
    if((Object.keys(result).length)==0)
    {
       e.preventDefault();
      this.invalid=true;
      setTimeout(()=>{
      this.invalid = false;
 }, 3000);
    }
    else{
      this.invalid=false;


// ******** If user is valid store his details on local Storage**********


    localStorage.clear();
    localStorage.setItem("user_info",JSON.stringify(result));
    localStorage.setItem("loggedIn","true");
    localStorage.setItem("no_of_bookings",this.bookingCount);
    this.route.navigate(['/profile']);
  }
  });
}
}
